	module.exports = {"data":{"markdownRemark":{"html":"<p>Riservato a chi ha completato con successo il per.corso sulla Ruota dell'Anno.</p>\n<p>Cosa può significare e comportare scegliere il sentiero dei sacerdote/ssa oggi?<br>\nQuesto percorso mira ad acquisire strumenti teorici e pratici da spendere nella propria vita, nella cultura e nella società di oggi. Essere sacerdote/ssa non significa affatto ricoprire un ruolo \"da mettere sul piedistallo\" ma è una scelta che richiede dedizione ed entusiasmo, definibile un vero e proprio stile di vita.<br>\nIl/la sacerdote/ssa non si \"innalza\" sopra agli altri, semmai scava più in profondità.</p>\n<p>I contenuti spaziano dalla ritualità pubblica e privata all'apprendimento di strumenti per costruire relazioni più efficaci, dalla sperimentazione di antiche pratiche utilizzate per millenni in passato all'attivismo nel mondo moderno.<br>\nAttualmente il per.corso si sviluppa nell'arco di un anno e parte ogni due anni, alternandosi con la Prima Spirale.</p>\n<p>Ulteriori informazioni saranno rese note con maggiori dettagli a chi deciderà di proseguire, una volta ultimato il primo per.corso sulla Ruota dell'Anno.</p>\n<p><strong>Il per.corso è aperto a donne e uomini.</strong></p>","frontmatter":{"path":"/corso-seconda-spirale","title":"In cammino con la Dea - Moderne pratiche sacerdotali"}}},"pathContext":{}}

